import { Divider, Collapse } from 'antd'

const { Panel } = Collapse



export default function Publications() {
    return <>
        <h2>INTERNATIONAL VENUES</h2>
        <Collapse bordered={false} defaultActiveKey={['6']} expandIconPosition='right' accordion>
            <Panel header={<p style={{ margin: 0 }}>[Regular Paper] Nam, Y., <b><u>Trirat, P.</u></b>, Kim, T., Lee, Y., and Lee, J. , "Context-Aware Deep Time-Series Decomposition for Anomaly Detection in Businesses," In Proc. European Conference on Machine Learning and Principles and Practice of Knowledge Discovery in Databases 2023 (ECML PKDD), Torino, Italy, Sep. 2023 (CORE A, acceptance rate: 24.3%)</p>} key="6">
                <div>
                    <p style={{ textAlign: 'justify', textIndent: '2.5em' }}>Detecting anomalies in time series has become increasingly challenging as data collection technology develops, especially in real-world communication services, which require contextual information for precise prediction. To address this challenge, researchers usually use time-series decomposition to reveal underlying patterns, e.g., trends and seasonality. However, existing decomposition-based anomaly detectors do not explicitly consider such contextual information, limiting their ability to correctly detect contextual cases. This paper proposes Time-CAD, a new context-aware deep time-series decomposition framework to detect anomalies for a more practical scenario in real-world businesses. We verify the effectiveness of the novel design for integrating contextual information into deep time-series decomposition through extensive experiments on four real-world benchmarks, demonstrating improvements of up to 46% in time-series aware F1 score on average.</p>
                    <p className="action"><a target="blank" href="https://link.springer.com/chapter/10.1007/978-3-031-43427-3_20">[PDF]</a> <a target="blank">[Poster]</a> <a target="blank">[PPT]</a> <a target="blank" href="https://github.com/kaist-dmlab/Time-CAD">[Code]</a></p>
                </div>
            </Panel>

            <Panel header={<p style={{ margin: 0 }}>[Demo Paper] <b><u>Trirat, P.*</u></b>, Nam, Y.*, Kim, T., and Lee, J., "AnoViz: A Visual Inspection Tool of Anomalies in Multivariate Time Series," In Proc. 37th AAAI Conference on Artificial Intelligence (AAAI), Washington DC, USA, Feb. 2023 (CORE A*)</p>} key="5">
                <div>
                    <p style={{ textAlign: 'justify', textIndent: '2.5em' }}>This paper presents AnoViz, a novel visualization tool of anomalies in multivariate time series, to support domain experts and data scientists in understanding anomalous instances in their systems. AnoViz provides an overall summary of time series as well as detailed visualizations of relevant detected anomalies in both query and stream modes, rendering near real-time visual analysis available. Here, we show that AnoViz streamlines the process of finding a potential cause of an anomaly with a deeper analysis of anomalous instances, giving explainability to any anomaly detector.</p>
                    <p className="action"><a target="blank" href="https://ojs.aaai.org/index.php/AAAI/article/view/27088">[PDF]</a> <a target="blank" href="fhttps://time-cad.web.app/files/AnoViz_AAAI23_Poster.pdf">[Poster]</a> <a target="blank" href="https://www.youtube.com/watch?v=fOHZO3xiMAA">[Video]</a> <a target="blank" href="https://time-cad.web.app">[Demo]</a> <a target="blank" href="https://github.com/kaist-dmlab/AnoViz">[Code]</a></p>
                </div>
            </Panel>
            <Panel header={<p style={{ margin: 0 }}>[Journal Article] <b><u>Trirat, P.</u></b>, Yoon, S., and Lee, J., "MG-TAR: Multi-View Graph Convolutional Networks for Traffic Accident Risk Prediction," IEEE Transactions on Intelligent Transportation Systems, Vol. 24, pp. 3779 ~ 3794, Apr. 2023 (SCI Expanded, impact factor: 9.551)</p>} key="4">
                <div>
                    <p style={{ textAlign: 'justify', textIndent: '2.5em' }}>Due to the continuing colossal socio-economic losses caused by traffic accidents, it is of prime importance to precisely forecast the traffic accident risk to reduce future accidents. In this paper, we use dangerous driving statistics from driving log data and multi-graph learning to enhance predictive performance. We first conduct geographical and temporal correlation analyses to quantify the relationship between dangerous driving and actual accidents. Then, to learn various dependencies between districts besides the traditional adjacency matrix, we simultaneously model both static and dynamic graphs representing the spatio-temporal contextual relationships with heterogeneous environmental data, including the dangerous driving behavior A graph is generated for each type of the relationships. Ultimately, we propose an end-to-end framework, called MG-TAR, to effectively learn the association of multiple graphs for accident risk prediction by adopting multi-view graph neural networks with a multi-attention module. Thorough experiments on ten real-world datasets show that, compared with state-of-the-art methods, MG-TAR reduces the error of predicting the accident risk by up to 23% and improves the accuracy of predicting the most dangerous areas by up to 27%.</p>
                    <p className="action"><a target="blank" href="https://ieeexplore.ieee.org/abstract/document/10023949">[PDF]</a> <a target="blank" href="https://github.com/kaist-dmlab/MG-TAR">[Code]</a></p>
                </div>
            </Panel>
            <Panel header={<p style={{ margin: 0 }}>[Regular Paper] <b><u>Trirat, P.</u></b> and Lee, J., "DF-TAR: A Deep Fusion Network for Citywide Traffic Accident Risk Prediction with Dangerous Driving Behavior," In Proc. The Web Conference 2021 (TheWebConf), Ljubljana, Slovenia, Apr. 2021 (CORE A*, acceptance rate: 20.6%)</p>} key="3">
                <div>
                    <p style={{ textAlign: 'justify', textIndent: '2.5em' }}>Because traffic accidents cause huge social and economic losses, it is of prime importance to precisely predict the traffic accident risk for reducing future accidents. In this paper, we propose a Deep Fusion network for citywide Traffic Accident Risk prediction (DF-TAR) with dangerous driving statistics that contain the frequencies of various dangerous driving offences in each region. Our unique contribution is to exploit these statistics, obtained by processing the data from in-vehicle sensors, for modeling the traffic accident risk. Toward this goal, we first examine the correlation between dangerous driving offences and traffic accidents, and the analysis shows a strong correlation between them in terms of both location and time. Specifically, quick start (0.83), rapid acceleration (0.76), and sharp turn (0.76) are the top three offences that have the highest average correlation scores. We then train the DF-TAR model using the dangerous driving statistics as well as external environmental features. By extensive experiments on various frameworks, the DF-TAR model is shown to improve the accuracy of the baseline models by up to 54% by virtue of the integration of dangerous driving into the modeling of traffic accident risk.</p>
                    <p className="action"><a target="blank" href="https://dl.acm.org/doi/10.1145/3442381.3450003">[PDF]</a> <a target="blank" href="https://docs.google.com/presentation/d/1UD-e4H6eAsabZ4UBe31kajLMJ2oYdPloXOC-072oUXU/edit?usp=sharing">[PPT]</a> <a target="blank" href="https://www.youtube.com/watch?v=XARHYVIvYPo">[Video]</a> <a target="blank" href="https://github.com/kaist-dmlab/DF-TAR">[Code]</a></p>
                </div>
            </Panel>
            <Panel header={<p style={{ margin: 0 }}>[Poster Paper] <b><u>Trirat, P.</u></b>, Noree, S. and Yi, M.Y., "IntelliMOOC: Intelligent Online Learning Framework for MOOC Platforms," In Proc. Educational Data Mining 2020 (EDM), Online, Jul. 2020 (CORE B)</p>} key="2">
                <div>
                    <p style={{ textAlign: 'justify', textIndent: '2.5em' }}>Massive Open Online Course (MOOC) has been inefficient in responding to students' questions, or in-lesson comments as the volume of questions is truly massive. This paper proposes a framework that utilizes students' behavioral data on the web in addition to text data. With this framework, we built a recommender system that generates a set of ranked video snippets in response to a student's question by implementing deep neural networks for question and confusion classifiers and adopting the idea of content-based recommendation to answer the student's question. Preliminary results show that our question and confusion classifiers outperform the baseline models. Our combined recommender model shows the best performance in recommending the answer. As an ongoing endeavor, we are in the process of developing an intelligent agent that leverages the confusion classifiers in improving student's achievement.</p>
                    <p className="action"><a target="blank" href="https://educationaldatamining.org/files/conferences/EDM2020/papers/paper_237.pdf">[Paper]</a> <a target="blank" href="https://www.youtube.com/watch?v=WQq5Dji2DM8">[Video]</a></p>
                </div>
            </Panel>
            <Panel header={<p style={{ margin: 0 }}>[Demo Paper] Shin, Y., Yoon, S., <b><u>Trirat, P.</u></b>, and Lee, J., "CEP-Wizard: Automatic Deployment of Distributed Complex Event Processing," In Proc. 35th Int'l Conf. on Data Engineering (IEEE ICDE), Macau, China, pp. 2004 ~ 2007, Apr. 2019 (CORE A*)</p>} key="1">
                <div>
                    <p style={{ textAlign: 'justify', textIndent: '2.5em' }}>Complex event processing (CEP) is defined as event processing for multiple stream sources to infer events that suggest complicated circumstances. As the size of stream data becomes larger, CEP engines have been parallelized to take advantage of distributed computing. Typically, deployment of such a distributed CEP engine involves manual configuration, which has been regarded as an obstacle to its widespread adoption. In this demonstration, we present CEP-Wizard, a framework of automatically configuring and deploying a distributed CEP engine with minimum effort. The demonstration shows that even inexperienced users can easily configure and deploy it on Apache Storm with achieving high performance and low resource usage.</p>
                    <p className="action"><a target="blank" href="https://ieeexplore.ieee.org/abstract/document/8731592">[Paper]</a> <a target="blank" href="https://drive.google.com/file/d/168ci6E9SyfKo8a1cv15144nQoT8QR3Cf/view">[Video]</a></p>
                </div>
            </Panel>
        </Collapse>
        <Divider>WRITING IS ITS OWN REWARD</Divider>
        <h2>DOMESTIC VENUES</h2>
        <Collapse bordered={false} expandIconPosition='right' accordion>
            <Panel header={<p style={{ margin: 0 }}>[Poster Paper] Na, J., Kim, J. <u><b>Trirat, P.</b></u>, Park, J., Park, S., Yu, K., and Lee, J, "Interpretable Relation Prediction of Undirected Hypergraphs with Dynamic Bipartite Graphs," In Proc. Korea Computer Congress 2023 (KIISE, KCC), Jeju, South Korea, pp. 911 ~ 913, Jun. 2023</p>} key="5">
                <div>
                    <p style={{ textAlign: 'justify', textIndent: '2.5em' }}>Predicting the number of new relationships based on combinations of people is a helpful task for decisionmaking. This paper proposes an Interpretable Relation Prediction (IRP) framework to predict the complex relationships between individuals and groups. Our two-stage framework uses a dynamic bipartite graph as an input to simplify increasing sets of nodes over time. The framework's high interpretability in handling temporal dynamics and intuitive features provides insights into the underlying factors driving the predictions, making it useful for decision-making in various domains, such as recommendation systems, social networks, and academic co-authorship networks.</p>
                    <p className="action"><a target="blank" href="files/KCC_2023.pdf">[Paper]</a></p>
                </div>
            </Panel>
            <Panel header={<p style={{ margin: 0 }}>[Poster Paper] <b><u>Trirat, P.</u></b>, Shin, Y., Kim, S., and Kim, M., "Generating a Machine Learning Model with a Few Sentences," In Proc. Korea Software Congress 2021 (KIISE, KSC), Pyeongchang, South Korea, pp. 688 ~ 690, Dec. 2021</p>} key="4">
                <div>
                    <p style={{ textAlign: 'justify', textIndent: '2.5em' }}>Towards better artificial intelligence democratization, we introduce a natural language processing application,  called  Conversational  AutoML,  combining  a  dialogue  system  and  automated  machine learning (AutoML) techniques. By focusing on connecting the dialogue system with existing AutoML via human language, in this work, we design the system to interact with users, parse their input sentences,  and  extract  the  information  needed  for  building  a  model.  The  final  output  of  the application is an actual machine learning model that the users can employ in their systems.</p>
                    <p className="action"><a target="blank" href="https://www.dbpia.co.kr/journal/articleDetail?nodeId=NODE11035819">[Paper]</a></p>
                </div>
            </Panel>
            <Panel header={<p style={{ margin: 0 }}>[Poster Paper] <b><u>Trirat, P.</u></b>, Kim, M., and Lee, J., "Experimental Analysis of the Effect of Autoencoder Architectures on Time Series Representation Learning," In Proc. Korea Computer Congress 2020 (KIISE, KCC), Online, pp. 842 ~ 844, Jul. 2020</p>} key="3">
                <div>
                    <p style={{ textAlign: 'justify', textIndent: '2.5em' }}>With the ever-growing amount of high-dimensional time-series data that is collected nowadays, it is critical to find the appropriate representation to work with various downstream tasks efficiently. To this end, recently, various approaches based on the autoencoder network to learn the time series representations are suggested, while there is a lack of experimental comparison among them. In this paper, we examine the effect of the different autoencoder architectures on time series representation learning. Our empirical findings surprisingly show that Convolution Neural Network (CNN) based designs are more effective in extracting representations of time-series data than other architectures.</p>
                    <p className="action"><a target="blank" href="https://www.dbpia.co.kr/journal/articleDetail?nodeId=NODE09874599">[Paper]</a></p>
                </div>
            </Panel>
            <Panel header={<p style={{ margin: 0 }}>[Journal Article] <b><u>Trirat, P.</u></b>, Yoon, S., and Lee, J., "Spatial and Temporal Analysis of Dangerous Driving Behavior Using DTG Data," Journal of Database Research, Vol. 35 No. 3, 2019, pp. 40-53</p>} key="2">
                <div>
                    <p style={{ textAlign: 'justify', textIndent: '2.5em' }}>In order to prevent traffic accidents, it is crucial to analyze the geographic and temporal characteristics of dangerous driving behavior. In this study, we analyze the potentially dangerous areas based on the Digital Tachograph (DTG) data together with the dangerous driving behavior criteria. Our findings showed that rapid acceleration (38.81%), sharp turn (24.72%), and overspeed (23.80%) are the top three most occurred dangerous driving behavior. Regarding the dangerous areas, our results revealed that there is a strong correlation between the dangerous driving behavior and the real traffic accidents which its correlation coefficient around 0.83. Within a week, Saturday (17.07%) shows the highest ratio of dangerous driving behavior occurrences. Within a day, between 6 AM to 8 AM (9.95%) show the highest ratio of dangerous driving behavior occurrences. By doing this, we conclude that our findings will be helpful to prevent future traffic accidents in both public transportation and private vehicles through traffic safety policy and traffic safety training for drivers.</p>
                    <p className="action"><a target="blank" href="https://www.kci.go.kr/kciportal/ci/sereArticleSearch/ciSereArtiView.kci?sereArticleSearchBean.artiId=ART002536454">[Paper]</a></p>
                </div>
            </Panel>
            <Panel header={<p style={{ margin: 0 }}>[Journal Article] <b><u>Trirat, P.</u></b>, Yoon, S., and Lee, J., "Study on Driving Behavior of Different Vehicle Types Using DTG Data," Journal of Database Research, Vol. 35 No. 2, 2019, pp. 78-92</p>} key="1">
                <div>
                    <p style={{ textAlign: 'justify', textIndent: '2.5em' }}>A traffic accident is a common problem in many countries because it causes social and economic issues. As the drivers driving behavior is the primary cause of traffic crashes, the purpose of this study is to utilize the DTG data along with the dangerous driving behavior criteria for analyzing and understanding the driving behavior of each vehicle type. We begin the study by exploring the data to understand the characteristics of each vehicle type and then analyze the day trip of a vehicle to detect how many times the dangerous driving behavior occurred. We find that most of the dangerous driving behavior occurred in the morning and afternoon during the weekends which results from overspeed (59%), sharp turn (32%), and sudden u-turn (7%). Additionally, the common areas where dangerous driving behavior occurred as the top 3 cities are Seoul and its vicinity, Gwangju, and Jeonju. Regarding vehicle types, occasional cars, rural buses, and school buses showed the highest ratio of dangerous driving behavior, whereas taxis, rural buses, and rental buses showed the highest number of occurrences. We expect that others can utilize our results as references for future traffic accident prevention policy or traffic safety education for drivers.</p>
                    <p className="action"><a target="blank" href="https://www.kci.go.kr/kciportal/ci/sereArticleSearch/ciSereArtiView.kci?sereArticleSearchBean.artiId=ART002495402">[Paper]</a></p>
                </div>
            </Panel>
        </Collapse>
    </>
}